.row {
    color: var(--white);
    margin-left: 20px;
    margin-right: 20px;

    .row__posters {
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll;
        padding: 20px;

        &::-webkit-scrollbar {
            display: none;
        }

        .row__poster {
            cursor: pointer;
            height: 100px;
            max-height: 100px;
            margin-right: 10px;
            object-fit: contain;
            width: 100%;
            transition: transform 450ms;

            @include respond(mobile) {
                &:hover {
                    transform: scale(1.08);
                }
            }
        }

        .row__posterLarge {
            height: 250px;
            max-height: 250px;

            @include respond(mobile) {
                &:hover {
                    transform: scale(1.09);
                }
            }
        }
    }
}
